/* 圖片超連結 */

.boxpic{
    width: 100%;
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    margin-top: 50px;
}
.boxpic>div{
    position: relative;
}
.boxpic>div>.txt{
    position:absolute;
    top:0;
    right: 0;
    left: 0;
    bottom: 0;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: large;
    background-color: rgba(0,0,0,.6);
    opacity: 0;
    color: #fff6f6;
    transition: opacity 0.5s;
}
.boxpic>div>.txt::after{
    content: '';
    display: block;
    width: 0%;
    height: 2px;
    margin: 5px 0;
    background-color:#fff6f6 ;
    transition: width 1s 0.2s ;
}
.boxpic>div>.txt:hover:after{
    width: 100%;
}

.boxpic>div>.txt:hover{
    opacity: 1;
    cursor: pointer;
}
.boxpic>div>img{
    width: 100%;
    height: 100%;
    vertical-align: middle;
}

/* 影片 */
.video{
    width: 100%;
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    margin-top: 50px;
    position: relative;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100%;
}
.video>.about{
    border: rgb(212, 212, 212) solid 1px;
    padding: 20px;
}
.video>div{
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    position: relative;
    -webkit-justify-content: center;
            justify-content: center;
}
.video>div>.txt{
    border-radius: 10px;
    padding: 5px 10px;
    color: white;
    position:absolute;
    border:white solid 2px ;
    display: -webkit-flex;
    display: flex;
    bottom: 25px;
    opacity: 0.5;
}
.video>div>.txt>a{
    color: white;
    text-decoration:none
}
.video>div>.txt:hover{
    cursor: pointer;
    background-color:#fb929e ;
    border:#fb929e solid 2px ;
    opacity: 1;
}

.video>div>img{
    vertical-align: middle;
    width: 100%;
    max-height: 30%;
}


/* 新聞 */
