}

@media screen and (max-width:1399px)and (min-width:1000px) {  /*螢幕1000~1399的*/
    .parrotul{
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        padding: 50px 0;

    } 
    .parrotul>li{
        width: 200px;
        height: 200px;
        margin: 2% 5% 8%;
        outline: #fb929e solid 3px;
        overflow:hidden;
        -webkit-transform:rotate(45deg) ;
                transform:rotate(45deg) ;
    }
    .parrotul>li>img{
        position: absolute;
        -webkit-transform:rotate(-45deg) ;
                transform:rotate(-45deg) ;
        width: 150%;
    }
    .parrotul>li>.txt{
        height: 100%;
    }
        
}


@media screen and (min-width:1400px) and (max-width:1999px) {  /*螢幕1400~2000的*/
    .parrotul{
        width: 75%;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        padding: 50px 0;

    } 
    .parrotul>li{
        width: 200px;
        height: 200px;
        margin: 0 5% -2%;
        outline: #fb929e solid 3px;
        overflow:hidden;
        -webkit-transform:rotate(45deg) ;
                transform:rotate(45deg) ;
    }
    .parrotul>li:nth-child(n+4){
        left: 150px;
        
    }
    .parrotul>li>img{
        position: absolute;
        -webkit-transform:rotate(-45deg) ;
                transform:rotate(-45deg) ;
        width: 150%;
    }
    .parrotul>li:nth-child(n+7){
        left: 0;
    }
    
}

@media screen and (min-width:2000px) {    /*螢幕2000以上的*/
