.ul{
    margin: auto;
    position: relative;
    width: 87%;
}
.parrotul{
    margin: auto;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

}
.parrotul>li{
    list-style:none;
    text-align: center;
    line-height: 200px;
    position: relative;
    font-size: 30px;

}
.parrotul>li>img{
    top: -70px;
    left: 25px;
    z-index: -1;
}
.parrotul>li>.txt{
    -webkit-transform:rotate(-45deg) ;
            transform:rotate(-45deg) ;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    color: white;
    opacity: 0.5;
    font-size:40px;
    border-radius: 50%;
    transition: font-size 0.5s;
}

.parrotul>li>.txt:hover{
    cursor: pointer;
    opacity: 1;
    font-size:50px;
    background-color: rgba(0,0,0,.6);
}


